import { Injectable } from '@angular/core';
import * as Pusher from 'pusher-js';

@Injectable({
  providedIn: 'root'
})

@Injectable()
export class PusherService {
  private _pusher: any;

  constructor() {
    this._pusher = new Pusher('ae6421390bb11fc21749', {
      cluster: 'eu',
      encrypted: true
    });
  }
  // any time it is needed we simply call this method
  getPusher() {
    return this._pusher;
  }
}
