import { DownloadModel } from './downloads.model';
import { Globals } from './../Globals';
import { Http, ResponseContentType } from '@angular/http';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AngularFirestore } from 'angularfire2/firestore';
import { Subject } from 'rxjs/Subject';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import { observable } from '../../../node_modules/rxjs';



@Injectable()
export class DownloadsService {

readonly baseURL = 'http://' + this.globals.ipaddress + ':3000/uploads/';


    constructor(private router: Router,
                private http: HttpClient,
            private globals: Globals) {
    }


   downloadFile(file: String) {
        const body = {filename: file};

        return this.http.post(this.baseURL + 'downloadfile', body, {
            responseType : 'blob',
            headers: new HttpHeaders().append('Content-Type' , 'application/json')
        });
    }

    downloadFilePayslip(file: String) {
        const body = {filename: file};

        return this.http.post(this.baseURL + 'downloadfilePayslip', body, {
            responseType : 'blob',
            headers: new HttpHeaders().append('Content-Type' , 'application/json')
        });
    }

    downloadEyeWitnessFile(file: String) {
        const body = {filename: file};

        return this.http.post(this.baseURL + 'downloadfileEyewitness', body, {
            responseType : 'blob',
            headers: new HttpHeaders().append('Content-Type' , 'application/json')
        });
    }





    GetListofAllMemos() {
        return this.http.get(this.baseURL +  'getallfiles');
    }


    GetListofAllPayslips(payslipyearmonth) {
        return this.http.post<DownloadModel[]>(this.baseURL +  'getallpayslip', payslipyearmonth);
    }







}
