import { Globals } from './../Globals';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { AngularFirestore } from 'angularfire2/firestore';
import { Subject } from 'rxjs/Subject';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import { UtmeCentres, PostingDetails, OtherPostingDetails, State, SituationReport,
    RegistrationReport, EyeWitnessReport } from './examinations.model';



@Injectable()
export class ExaminationService {

readonly baseURL = 'http://' + this.globals.ipaddress + ':3000/examination/';

    constructor(private router: Router,
                private http: HttpClient,
            private globals: Globals) {
    }

    GetUTMECentres() {
    return this.http.get<UtmeCentres[]>(this.baseURL + 'utmecentres');
    }

    GetRegistrationCentres() {
        return this.http.get<UtmeCentres[]>(this.baseURL + 'regcentres');
        }


    GetPostings(idcard: any) {
   return this.http.get<PostingDetails[]>(this.baseURL + 'postings' + '/' + idcard);
    }


    GetPostingsOthers(idcard: any, twncode: any) {
        return this.http.get<OtherPostingDetails[]>(this.baseURL + 'otherpostings' + '/' + idcard + '/' + twncode);
    }

    GetUTMEState() {
        return this.http.get<State[]>(this.baseURL + 'utmestates');
    }
    GetUTMESituation() {
        return this.http.get<SituationReport[]>(this.baseURL + 'sitreport');
    }

    GetAllUTMERegistration() {
        return this.http.get<RegistrationReport[]>(this.baseURL + 'regreportall');
    }

    GetAllEyeWitnessReport() {
        return this.http.get<EyeWitnessReport[]>(this.baseURL + 'eyewitnessreport');
    }


    GetUTMERegistration(loggedby: string) {
        return this.http.get<RegistrationReport[]>(this.baseURL + 'regreport' + '/' + loggedby);
    }

    UpdateUTMESituation(sitreport: SituationReport, issuestat: string) {
       return this.http.post(this.baseURL + 'updatesitreport', {sitreport: sitreport.SerialNo, issuestat: issuestat});

    }

    UpdateRegistration(regreport: RegistrationReport, issuestat: string) {
        return this.http.post(this.baseURL + 'updateregreport', {regreport: regreport.SerialNo, issuestat: issuestat});
     }




    InsertRegistrationReport(regreport: any, loggedby: string) {
        return this.http.post(this.baseURL + 'insertregreport',
        {   StateName: regreport.LogState,
            CentreName: regreport.LogCentre,
            IssueCategory: regreport.LogIssueCategory,
            ProblemDesc: regreport.LogProblem,
            Solution: regreport.LogSolution,
            IssueStatus: regreport.LogIssueStatus,
             loggedby: loggedby});
    }

    InsertEyeWitnessReport(regreport: any, loggedby: string, filename: string) {
            return this.http.post(this.baseURL + 'eyewitnessreport',
            {   StateName: regreport.LogState,
                CentreName: regreport.LogCentre,
                ProblemDesc: regreport.LogProblem,
                filename: filename,
                 loggedby: loggedby});
        }









}
