@Injectable()
export class AuthService {
    authchange = new Subject<boolean>();

// Remeber to change isauthenticated to False, if you want page to be loggng out every time page refreshes

    private isAuthenticated = false;
    private errorMessage;

    constructor (private router: Router,
        private globals: Globals,
      // private AfAuth: AngularFireAuth,
       // private db: AngularFirestore,
        private http: HttpClient) {
            const settings = {timestampsInSnapshots: true};
       // db.app.firestore().settings(settings);
    }

     /*  LoginUser (authdata: AuthData) {
        this.AfAuth.auth.signInWithEmailAndPassword(
            authdata.email,
            authdata.password
         )
         .then (result => {
             this.isAuthenticated = true;
             this.router.navigate(['/auth/dashboard']);
         })
         .catch (err => {
             this.errorMessage = 'Invalid Login Credentials';
             console.log(err);
         });
     } */

     LoginUser (authdata: AuthData) {
        const baseURL = 'http://' + this.globals.ipaddress + ':3000/user/';
       this.isAuthenticated = true;
       return this.http.get<User[]>(baseURL + authdata.email + '/' + authdata.password);
     }


    LogOut() {
        this.isAuthenticated = false;
        this.authchange.next(false);
        localStorage.clear();
        this.router.navigate(['/login']);
    }

    GetUser () {
    }

    IsAuth () {
        return this.isAuthenticated;
    }


}


