import { Globals } from './../Globals';

import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { AngularFirestore } from 'angularfire2/firestore';
import { Subject } from 'rxjs/Subject';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import { StaffDetails } from './staff.model';
import { Department } from './department.model';
import { Options } from '../../../node_modules/@types/selenium-webdriver/safari';
import { observable } from '../../../node_modules/rxjs';



@Injectable()
export class StaffService {
// private staffdetails: StaffDetails[] = [];
private department: Department[] = [];
dialogData: any;
// UtmeCentresChanged = new Subject<StaffDetails[]>();
// FetchedUtmeCentres = new Subject<StaffDetails[]>();
readonly baseURL = 'http://' + this.globals.ipaddress + ':3000/staff/';


    constructor(private router: Router,
                private http: HttpClient,
            private globals: Globals) {
    }

    GetStaffdetails() {
    return this.http.get<StaffDetails[]>(this.baseURL + 'staffdetails');
    }


    GetAllDepartment() {
        return this.http.get<Department[]>(this.baseURL + 'department');
    }


    AddNewStaff(newstaffdetails) {
        return this.http.post(this.baseURL + 'addnew', newstaffdetails);
    }

    UpdateStaff(staffdetails: StaffDetails) {
        console.log(staffdetails);
       return this.http.post(this.baseURL + 'updatestaff', staffdetails);

    }

    UpdateProfileDetails(updateprof: any) {
       return this.http.post(this.baseURL + 'updateprof', updateprof);
    }



}
